.not-found {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    text-align: center;
  }
  
  h1 {
    font-size: 6rem;
    margin-bottom: 1rem;
    color: #f44336; /* red color */
  }
  
  h2 {
    font-size: 3rem;
    margin-bottom: 2rem;
    color: #333; /* dark gray color */
  }
  
  p {
    font-size: 1.5rem;
    color: #666; /* light gray color */
  }
  